@use '../globals' as *;

.button {
    border: 1px solid #fff;
    border-style: solid;
    border-width: 1px;
    @include theme('border-color', 'txt');
    outline: none;
    padding: 1em 1.5em;
    font-weight: 600;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    background: none;
    color: inherit;
    cursor: pointer;
}